<template>
    <div>  
        <div class="loginNav">
            <img src="../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">请假</p>
        </div>
        <div style="padding:0.84rem 0 1.7rem;">
            <div class="tap f28">
                <button v-bind:class="active ? 'bg_6b7' : 'b7f'" @click="not">未审批</button>
                <button v-bind:class="!active ? 'bg_6b7' : 'b7f'" @click="yes">已审批</button>
            </div>
            <div class="pl-30">
                <ul v-if="active">
                    <li class="pr-30 b_line" v-for="(item,index) in list" :key="index" @click="lookDetail(index,item.id,2)">
                        <div>
                            <p class="f28 c333">{{item.studentName}}</p>
                            <!-- <img :style="{display:item.isSee?'none':'inline-block'}" src="../../assets/message/notSee.png" alt=""> -->
                        </div>
                        <div class="mt-22">
                            <p class="f26 c666" style="overflow:hidden;text-overflow:ellipsis;white-space:nowrap;width:80%;">{{item.content}}</p>
                            <p class="f18 c999">{{item.createTime | fileTime(item.createTime)}}</p> 
                        </div>
                    </li>
                </ul>
                 <ul v-else>
                    <li class="pr-30 b_line" v-for="(item,index) in list1" :key="index" @click="lookDetail(index,item.id,1)">
                        <div>
                            <p class="f28 c333">{{item.studentName}}</p>
                            <!-- <img :style="{display:item.isSee?'none':'inline-block'}" src="../../assets/message/notSee.png" alt=""> -->
                        </div>
                        <div class="mt-22">
                            <p class="f26 c666" style="overflow:hidden;text-overflow:ellipsis;white-space:nowrap;width:80%;">{{item.content}}</p>
                            <p class="f18 c999">{{item.createTime | fileTime(item.createTime)}}</p> 
                        </div>
                    </li>
                </ul>
            </div>
        </div>
        <!-- <div class="centerBtn f26 cfff text-center">确定创建</div> -->
    </div>
</template>

<script>
import {publisFn} from '../../lib/util'
export default {
  name: 'HelloWorld',
    data () {
        return {
            msg: 'Welcome to Your Vue.js App',
            active: true,
            active1: false,
            list:[],//已审批
            list1:[],
            param : {//未审批
                teacherId : JSON.parse(localStorage.getItem('zhxy_user')).id,
                states : 2,
                page : 1,
                size : 5
            },
            param1 : {//已审批
                teacherId : JSON.parse(localStorage.getItem('zhxy_user')).id,
                states : 1,
                page : 1,
                size : 5
            }
        }
    },
    filters : {
        fileTime(num){
           return publisFn.dateFormat(num).substring(0,10)
        }
    },
    methods : {
        returns(){
            this.$router.go(-1);//返回上一层
        },
        not(){
            this.active=true;
            this.list = [];
            this.getAllLeave();
        },
        yes(){
            this.list1 = [];
            this.active=false;
            this.getAllLeave();
        },
        lookDetail(index,id,type){
            // var list=this.list;
            // list[index].isSee=true;
            // this.list=list;
            this.$router.push({path:'/leaveDetail',query:{active:this.active,id:id,type:type}})
        },
        getAllLeave(){
            this.$toast.loading({
                mask: true,
                duration:0
            });
            if(this.active){
                var param = this.param;
            }else{
                var param = this.param1;
            }
            this.$http({url:this.$api.message.getAllLeave},param).then(data => {
                console.log(data);
                if(data.code == 0){
                    if(this.active){
                        for(var i in data.data){
                            this.list.push(data.data[i]);
                        }
                    }else{
                        for(var i in data.data){
                            this.list1.push(data.data[i]);
                        }
                    }
                    this.$toast.clear();
                }else{
                    this.$toast(data.msg);
                }
                
            },err => {
                this.$toast('出错了');
            })
        }
    },
    created() {
        this.getAllLeave();
    },
}
</script>

<style scoped>
     /* 修改组件样式 */
    .loginNav{
        position: fixed;
        z-index: 99999999 !important;
    }
    .van-tabbar-item--active {color: #8a9cff;}
    .tap{
        display: flex;
        justify-content: space-between;
        margin:0.3rem 0.3rem 0.2rem;
        height:.6rem;
        border-radius: 0.15rem;
        overflow: hidden;
        border:0.02rem solid #6B77FF;
        color:#fff;
    }
    .tap button{
        width:50%;
        border:0;
        background:#fff;
    }
    li{
        padding:0.3rem 0.3rem 0.2rem 0;
    }
    li div{
        display: flex;
        justify-content: space-between;
        align-items: center
    }
    li img{
        width:0.14rem;
        height:0.14rem;
    }
    .centerBtn{
        height: 0.9rem;
        border-radius: 0.9rem; 
        background: linear-gradient(to bottom right, #82cbff , #7b7ceb);
        line-height: 0.9rem; 
        box-shadow: 0 0 10px #f0f9ff;
        position: fixed;
        bottom: 0.5rem;
        left:1rem;
        right:1rem;
    }
</style>




